import React from 'react';
import './order.css'
const Order = () => {
  const handleSubmit = (e) => {
    e.preventDefault();
    const formData = new FormData(e.target);
    const data = Object.fromEntries(formData.entries());

   
    alert(
      ` :\n` +
      `: ${data.firstName}\n` +
      `: ${data.lastName}\n` +
      `: ${data.address}\n` +
      `Email: ${data.email}\n` +
      `: ${data.phone}\n` +
      `: ${data.quantity}\n` +
      ` : ${data.paymentMethod}`
    );
  };

  return (
    <section id="order" className="order">
      <h2> </h2>
      <form onSubmit={handleSubmit}>
        <div>
          <label htmlFor="firstName"></label>
          <input
            type="text"
            id="firstName"
            name="firstName"
            required
            placeholder=" "
          />
        </div>

        <div>
          <label htmlFor="lastName"></label>
          <input
            type="text"
            id="lastName"
            name="lastName"
            required
            placeholder=" "
          />
        </div>

        <div>
          <label htmlFor="address"></label>
          <input
            type="text"
            id="address"
            name="address"
            required
            placeholder=" "
          />
        </div>

        <div>
          <label htmlFor="email">Email</label>
          <input
            type="email"
            id="email"
            name="email"
            required
            placeholder=" email"
          />
        </div>

        <div>
          <label htmlFor="phone"></label>
          <input
            type="tel"
            id="phone"
            name="phone"
            required
            pattern="^\+?[0-9]{10,15}$"
            placeholder="  "
          />
        </div>

        <div>
          <label htmlFor="quantity"></label>
          <input
            type="number"
            id="quantity"
            name="quantity"
            required
            min="1"
            placeholder=" "
          />
        </div>

        <div>
          <label htmlFor="paymentMethod"> </label>
          <select id="paymentMethod" name="paymentMethod" required>
            <option value="card"></option>
            <option value="cash_on_delivery"> </option>
            <option value="sbp"></option>
          </select>
        </div>

        <button type="submit"> </button>
      </form>
    </section>
  );
};

export default Order;



