import React, { useState } from "react";
import "./contacts.css";

const Contacts = () => {
  const [formData, setFormData] = useState({
    name: "",
    phone: "",
    email: "",
    message: "",
  });
  const [errors, setErrors] = useState({
    name: "",
    phone: "",
    email: "",
    message: "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const validateForm = () => {
    let isValid = true;
    const newErrors = { name: "", phone: "", email: "", message: "" };

    //  
    if (!formData.name) {
      newErrors.name = "    ";
      isValid = false;
    } else if (!/^[a-zA-Z--]+$/.test(formData.name)) {
      newErrors.name = "    ";
      isValid = false;
    }

    //  
    if (!formData.phone) {
      newErrors.phone = "    ";
      isValid = false;
    } else if (!/^\+7[\s-]?\(?\d{3}\)?[\s-]?\d{3}[\s-]?\d{2}[\s-]?\d{2}$/.test(formData.phone) && !/^\+?\d{11}$/.test(formData.phone)) {
      newErrors.phone = "     +7XXXXXXXXXX    ";
      isValid = false;
    }

    //  
    if (!formData.email) {
      newErrors.email = "    ";
      isValid = false;
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = "  ";
      isValid = false;
    }

    //  
    if (!formData.message) {
      newErrors.message = "    ";
      isValid = false;
    }

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validateForm()) {
      //   -    alert
      alert(` :
: ${formData.name}
: ${formData.phone}
: ${formData.email}
: ${formData.message}`);
    }
  };

  return (
    <div id="contacts">
      <div className="left-container">
        <div className="contact-info">
          <p> <strong> :</strong><br />, . , . 5</p>
          <p> <strong>:</strong><br />+7 (900) 123-45-67</p>
          <p> <strong>E-mail:</strong><br />hello@pinkpineapple.ru</p>
          <p> <strong> :</strong><br />: <span className="bold">9:00  20:00</span><br />: <span className="bold">10:00  18:00</span></p>
          <p>     <br/>       !</p>
        </div>
      </div>

      <div className="right-container">
        <h1> </h1>
        <form onSubmit={handleSubmit}>
          <label>
            
            <input 
              type="text" 
              name="name" 
              placeholder="  " 
              value={formData.name} 
              onChange={handleChange} 
              className={errors.name ? "error-input" : ""}
            />
            {errors.name && <span className="error-message">{errors.name}</span>}
          </label>
          <label>
            
            <input 
              type="tel" 
              name="phone" 
              placeholder="  " 
              value={formData.phone} 
              onChange={handleChange} 
              className={errors.phone ? "error-input" : ""}
            />
            {errors.phone && <span className="error-message">{errors.phone}</span>}
          </label>
          <label>
            
            <input 
              type="email" 
              name="email" 
              placeholder="  " 
              value={formData.email} 
              onChange={handleChange} 
              className={errors.email ? "error-input" : ""}
            />
            {errors.email && <span className="error-message">{errors.email}</span>}
          </label>
          <label>
             
            <textarea 
              name="message" 
              placeholder="  " 
              value={formData.message} 
              onChange={handleChange} 
              className={errors.message ? "error-input" : ""}
            ></textarea>
            {errors.message && <span className="error-message">{errors.message}</span>}
          </label>
          <button type="submit"></button>
        </form>
      </div>
    </div>
  );
};

export default Contacts;



