import React from "react";


const Header = () => {
  const handleScroll = (id) => {
    const section = document.getElementById(id);
    if (section) {
      section.scrollIntoView({ behavior: "smooth" });
    }
  };

  return (
    <header className="header">
      <nav className="navigate">
        <ul className="nav-list">
          <li onClick={() => handleScroll("contacts")}></li>
          <li onClick={() => handleScroll("about")}> </li>
          <li onClick={() => handleScroll("order")}></li>
          <li onClick={() => handleScroll("main")}></li>
          <li onClick={() => handleScroll("product")}></li>
          <li onClick={() => handleScroll("our_advantages")}> </li>
          <li onClick={() => handleScroll("reviews")}></li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;



